import React from 'react';
import PostBox from '../components/PostBox/PostBox';
import { graphql, StaticQuery } from 'gatsby';

function RecentPost(props) {
  const posts = props.data.allMdx.posts || [];
  return (
    <>
      {posts.length > 0 && (
        <ul>
          {posts.map(({ post }) => {
            return (
              <li key={post.fields.title}>
                <PostBox post={post} />
              </li>
            );
          })}
        </ul>
      )}
    </>
  );
}

export default () => (
  <StaticQuery
    query={graphql`
      query {
        allMdx(sort: { fields: [fields___date, fields___title], order: DESC }, limit: 3) {
          posts: edges {
            post: node {
              fields {
                slug
                date(formatString: "YYYY-MM-DD")
                title
                category
                tags
              }
              timeToRead
            }
          }
        }
      }
    `}
    render={(data) => <RecentPost data={data} />}
  />
);
