/**@jsx jsx */
import { jsx } from 'theme-ui';
import React from 'react';
import styled from '@emotion/styled';
import kebabCase from 'lodash/kebabCase';
import { Link } from 'gatsby';

const StyledLink = styled(Link)`
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  height: 25px;
  text-align: center;
  padding: 0 10px;
  transition: color 100ms ease-out;
  font-size: 14px;
`;

const StyledDiv = styled.div`
  margin-left: 5px;
  display: inline-block;
  height: 16px;
  text-align: center;
  min-width: 25px;
  padding-left: 2px;
  padding-right: 2px;
  font-size: 12px;
  font-weight: bold;
  border-radius: 1rem;
`;

export default function PostTag(props) {
  return (
    <StyledLink
      to={`/tags/${kebabCase(props.tag)}`}
      sx={{
        border: '1px solid',
        borderColor: 'offblack',
        borderRadius: 1,
        backgroundColor: 'background',
        color: 'offblack',
        '&:hover': {
          color: 'primary',
          borderColor: 'primary',
        },
      }}
    >
      {props.tag}
      {props.count !== undefined && (
        <StyledDiv
          sx={{
            color: 'white',
            backgroundColor: 'primary',
          }}
        >
          <p
            sx={{
              lineHeight: 'dense',
            }}
          >
            {props.count}
          </p>
        </StyledDiv>
      )}
    </StyledLink>
  );
}
