/**@jsx jsx */
import { jsx } from 'theme-ui';
import React from 'react';
import { Link } from 'gatsby';
import Tag from '../PostTag/PostTag';
import kebabCase from 'lodash/kebabCase';
import styled from '@emotion/styled';

const StyledPostBox = styled.div`
  margin-top: 1rem;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  min-height: 133px;
  padding-left: 1.6rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 1.1rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-evenly;
  transition: all 200ms ease-in;
  &:hover {
    transform: translateY(-4px);
  }
  .row1 {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }
  .row2 {
    margin-top: 0.3rem;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
  }
  > .row3 {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-around;
  }
`;

export default function PostBox({ post }) {
  const fields = post.fields || {};
  const slug = post.slug || fields.slug;
  const title = post.title || fields.title;
  const intro = post.intro;
  const tags = post.tags || fields.tags || [];
  const category = post.category || fields.category || 'MISC';
  const date = post.date || fields.date;
  const timeToRead = post.timeToRead || null;
  return (
    <StyledPostBox
      sx={{
        backgroundColor: 'offwhite',
      }}
    >
      <div className="row1">
        <div>
          <Link
            to={`/categories/${kebabCase(category)}`}
            sx={{
              fontSize: 1,
              color: 'black',
              fontWeight: 'semibold',
              fontStyle: 'italic',
              '&:hover': {
                color: 'primary',
              },
            }}
          >
            <h3>{category}</h3>
          </Link>
        </div>
        {timeToRead && (
          <div
            sx={{
              fontSize: 1,
              color: 'offblack',
            }}
          >
            <p>{timeToRead} min read</p>
          </div>
        )}
      </div>
      <div className="row2">
        <Link
          to={`/post/${slug}`}
          sx={{
            color: 'black',
            fontSize: 4,
            '&:hover>h2': {
              backgroundSize: '77% 100%',
            },
            '&>h2': {
              display: 'inline',
              backgroundImage:
                'linear-gradient(to bottom,transparent 50%,hsl(260, 100%, 80%) 50%,hsl(260, 100%, 80%) 85%,transparent 85%,transparent 100%)',
              backgroundRepeat: 'no-repeat',
              backgroundSize: '0 100%',
              transition: '.5s cubic-bezier(.6,.04,.35,1)',
            },
          }}
        >
          <h2>{title}</h2>
        </Link>
        <div
          sx={{
            paddingRight: '0.5rem',
            fontSize: 1,
            color: 'offblack',
          }}
        >
          <small>{date}</small>
        </div>
      </div>
      <div
        className="intro"
        sx={{
          my: 5,
        }}
      >
        {intro}
      </div>
      <div className="row3">
        <div>
          <span
            sx={{
              color: 'offblack',
              fontSize: 0,
              fontWeight: 'lighter',
            }}
          >
            Tags:
          </span>
          <ul
            sx={{
              paddingLeft: 1,
              display: 'inline-flex',
              flexFlow: 'row wrap',
            }}
          >
            {tags.length > 0 &&
              tags.map((tag, index) => {
                return (
                  <li
                    key={index}
                    sx={{
                      paddingLeft: 1,
                    }}
                  >
                    <Tag tag={tag} />
                  </li>
                );
              })}
          </ul>
        </div>
      </div>
    </StyledPostBox>
  );
}
