import styled from '@emotion/styled';
import FloatText from '../animations/FloatText';

const StyledCard = styled.div`
  padding-bottom: 2em;
`;

const StyledTitle = styled.div`
  margin: 1em;
  text-align: center;
`;

const CardBox = ({ title, children, isFloat = false, ...rest }) => (
  <StyledCard>
    <StyledTitle>
      {isFloat && <FloatText str={title} style={{ fontSize: '1.5rem' }} />}
      {!isFloat && (
        <div {...rest} style={{ fontSize: '1.5rem' }}>
          {title}
        </div>
      )}
    </StyledTitle>
    {children}
  </StyledCard>
);

export default CardBox;
